/* src/components/Projects/Projects.css */

#projects {
    padding: 50px 20px;
    background-color: #f9f9f9;
    text-align: center;
  }
  
  #projects h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 30px;
    cursor: pointer;
  }
  
  .projects-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    justify-items: center;
    margin-top: 10px;
  }
  
  .project-card {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    max-width: 100%;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    text-align: left;
  }
  
  .project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
  
  .project-card h3 {
    font-size: 1.5rem;
    color: #007acc;
    margin-bottom: 15px;
  }
  
  .project-card p {
    font-size: 1rem;
    color: #666;
    margin-bottom: 20px;
  }
  
  .project-link {
    background-color: #007acc;
    color: #fff;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-weight: bold;
    transition: background-color 0.3s ease;
    align-self: flex-start;  /* Align button to the left */
    margin-top: auto; 
  }
  
  .project-link:hover {
    background-color: #005f99;
  }
  
  .project-image {
    width: 100%;                /* Makes the image span the full width of the card */
    height: 200px;              /* Sets a fixed height for consistency */
    object-fit: cover; 
    border-radius: 8px;
    margin-bottom: 5px;
  }
  