/* workexperience.css */

.work-experience-section {
    padding: 2em;
    background-color: #f9f9f9;
    font-family: Arial, sans-serif;
  }
  
  .work-experience-section h2 {
    text-align: center;
    font-size: 2.5em;
    color: #333;
    margin-bottom: 1em;
  }
  
  .work-experience-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
  }
  
  .experience-entry {
    display: flex;
    max-width: 900px;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    flex-direction: row;
  }
  
  .experience-icon img {
    width: 100px;
    height: 100px;
    padding: 1em;
    background-color: #eef;
    background-image: url('img1.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  
  .experience-details {
    padding: 1.5em;
    display: flex;
    flex-direction: column;
    gap: 1em;
  }
  
  .experience-details h3 {
    font-size: 1.5em;
    color: #333;
    margin-bottom: 0.5em;
  }
  
  .experience-details ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    color:#333
  }
  
  .experience-details ul li {
    margin-bottom: 0.8em;
    font-size: 1em;
    line-height: 1.6;
  }
  
  .experience-details p {
    font-size: 1em;
    color: #666;
  }
  
  @media (max-width: 768px) {
    .experience-entry {
      flex-direction: column;
      text-align: center;
    }
  
    .experience-icon img {
      margin: 0 auto;
    }
  }
  