/* General styling for the Skills section */
#skills {
    background-color: #f2f2f2; /* Light gray background */
    padding: 2rem; /* Padding around the section */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    text-align: center; /* Center the text */
}

h2 {
    font-size: 2rem; /* Larger font size for emphasis */
    color: #333; /* Darker text color */
    margin-bottom: 1rem; /* Space below the title */
}

.skills {
    overflow: hidden; /* Hide overflow for marquee effect */
    position: relative; /* Relative positioning for absolute elements */
}

/* Marquee styling */
.marquee {
    display: flex; /* Use flexbox for horizontal scrolling */
    white-space: nowrap; /* Prevents items from wrapping */
    overflow: hidden; /* Hides overflow content */
    animation: marquee 10s linear infinite; /* Adjust speed as needed */
}

.marquee.paused {
    animation-play-state: paused; /* Pause the animation when hovered */
}

.marquee a {
    margin: 0 3rem; /* Space between images */
}

/* Image styling */
.images {
    width: 120px; /* Set a fixed width for images */
    height: 120px; /* Maintain aspect ratio */
    transition: transform 0.2s; /* Smooth transition for hover effect */
    border: 2px solid #ffb400;
}

.images:hover {
    transform: scale(1.1); /* Enlarge image slightly on hover */
}

/* Animation keyframes for marquee effect */
@keyframes marquee {
    from { transform: translateX(100%); } /* Start off-screen to the right */
    to { transform: translateX(-100%); } /* End off-screen to the left */
}
